.service-type,.price{ display: inline-block; }
.price{ padding-left: 30px; }
.weui_cells_radio .weui_check:checked+.zj-checked:before{ color: #03A9F4; }
.weui_cells .zj-label-border{ border-bottom: 1px solid #d9d9d9; }
.weui_cells .zj-label-border:before{ border-top: 0; }


/*使用和修改了addManagement.css中的样式*/
*{ margin: 0; padding: 0; }
.choose-addr{ width: 750px; margin: 0 auto; }
.choose-addr .addr-tip{ margin-top: 500px; text-align: center; }
.choose-addr .addr-tip .mark{ color: red; }
.choose-addr .addr-bottom{ margin: 0 auto; width: 750px; position: fixed; bottom: 0; height: 80px; background: red; width: 750px; text-align: center; line-height: 80px; }

/*.choose-addr .addr-lists{ height: 1000px; overflow-y: auto; }*/
.choose-addr .zj-movebar{ width: 750px; }
.choose-addr .weui_panel_bd .zj-addr-list{ height: 80px; width: 718px; border: 1px solid #000; }
.choose-addr .list-name{  }
.choose-addr .list-gender{ position: relative; left: 103px; top: -45px; }
.choose-addr .list-phone{ position: relative; left: 186px; top: -87px }
.choose-addr .list-symbol{ width: 20px; height: 20px; background: red; position: relative; top: -42px; left: 10px; }
.choose-addr .list-address{ position: relative; top: -72px; left: 47px; }
.choose-addr .list-edit{ position: relative; top: -159px; right: -650px; }
.choose-addr .weui_panel_bd .list-delete{ width: 50px; height: 112px; background: red; }


.choose-addr .line-wrapper { width: 100%; height: 144px; font-size: 28px; border-bottom: 1px solid #aaa; }
.choose-addr .line-scroll-wrapper { white-space: nowrap; height: 144px; clear: both; }
.choose-addr .line-btn-delete { float: left; width: 132px; height: 144px; }
.choose-addr .line-btn-delete button { width: 100%; height: 100%; background: red; border: none; font-size: 24px; font-family: 'Microsoft Yahei'; color: #fff; }
.choose-addr .line-normal-wrapper { display: inline-block; padding-top: 10px; padding-bottom: 10px; }
.choose-addr .line-normal-icon-wrapper { float: right; width: 120px; height: 120px; margin-right: 12px; }
.choose-addr .line-normal-avatar-wrapper { width: 100px; height: 124px; float: left; margin-left: 12px; }
.choose-addr .line-normal-left-wrapper {  }
.choose-addr .line-normal-info-wrapper { margin-left: 10px; }
.choose-addr .line-normal-user-name { height: 28px; line-height: 28px; color: #4e4e4e; margin-top: 7px; }
.choose-addr .line-normal-msg { height: 28px; line-height: 28px; overflow:hidden; text-overflow:ellipsis; color: #4e4e4e; margin-top: 11px; }
.choose-addr .line-normal-time { height: 28px; line-height: 28px; color: #999; margin-top: 11px; }